import { Button } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import React, { useState } from 'react';
import { Color } from '../styles/styles';

export interface Props {
  classes: {
    loginContainer: string;
    loginForm: string;
    loginInput: string;
    loginButtonGroup: string;
    loginButton: string;
  };
  login: Function;
}

const styles: object = (theme) => ({
  loginContainer: {
    height: 360,
    backgroundColor: Color.secondColor,
    boxShadow: '0px 2px 4px -1px rgba(0,0,0,0.2),'
      + '0px 4px 5px 0px rgba(0,0,0,0.14),'
      + '0px 1px 10px 0px rgba(0,0,0,0.12)',
  },
  loginForm: {
    display: 'flex',
    flexDirection: 'column',
  },
  loginInput: {
    marginTop: 48,
    backgroundColor: Color.gray,
  },
  loginButtonGroup: {
    display: 'flex',
    justifyContent: 'center',
  },
  loginButton: {
    marginTop: 48,
    backgroundColor: Color.cyan,
    '&:hover': {
      backgroundColor: Color.cyan700,
    },
  },
  [theme.breakpoints.down('sm')]: {
    loginContainer: {
      width: '100%',
    },
  },
  [theme.breakpoints.up('sm')]: {
    loginContainer: {
      width: 800,
      margin: '0 auto',
      marginTop: '8em',
    },
  },
});

const NewTextField = withStyles({
  root: {
    '& label.Mui-focused': {
      color: Color.cyan700,
    },
    '& .MuiFilledInput-underline::after': {
      borderBottom: `4px solid ${Color.cyan700}`,
    },
  },
})(TextField);

const LoginView = ({
  classes,
  login,
}: Props): React.ReactElement => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  return (
    <Container className={classes.loginContainer} maxWidth="sm">
      <form className={classes.loginForm}>
        <NewTextField
          id="email"
          label="Epost"
          className={classes.loginInput}
          type="text"
          autoComplete="current-password"
          margin="normal"
          variant="filled"
          value={email}
          onChange={(event) => setEmail(event.target.value)}
        />
        <NewTextField
          id="password"
          label="Passord"
          className={classes.loginInput}
          type="password"
          autoComplete="current-password"
          margin="normal"
          variant="filled"
          value={password}
          onChange={(event) => setPassword(event.target.value)}
        />
        <div className={classes.loginButtonGroup}>
          <Button
            variant="contained"
            className={classes.loginButton}
            onClick={() => login(email, password)}
          >
            Logg inn
          </Button>
        </div>
      </form>
    </Container>
  );
};

export default withStyles(styles)(LoginView);
